import { useTickets } from "../context/FiltrosContext";

const FiltroDistance = () => {
  const { distanceFilter, setDistanceFilter } = useTickets();

  return (
    <div className="filtro-item">
      <select
        value={distanceFilter}
        onChange={(e) => setDistanceFilter(Number(e.target.value))}
      >
        <option value="500">At 500 metros</option>
        <option value="1000">At 1 km</option>
        <option value="2000">At 2 kms</option>
        <option value="5000">At 5 kms</option>
        <option value="10000">At 10 kms</option>
        <option value="20000">At 20 kms</option>
        <option value="40000">At 40 kms</option>
      </select>
    </div>
  );
};

export default FiltroDistance;
