import { createContext, useContext } from "react";

const InfoContext = createContext();

export const InfoProvider = ({ children }) => {
  const info = {
    title: "Ajuda+",
    description: "Uma plataforma para oferecer e receber ajuda no seu bairro.",
  };

  const categorias = [
    {
      id: "necessidades_basicas",
      nome: "Necessidades Bsicas",
      descricao:
        "Pedidos ou ofertas de alimentos, roupas, fraldas, mveis, brinquedos, materiais escolares e outros itens essenciais.",
      icone: "shopping-basket",
    },
    {
      id: "cuidados_e_apoio",
      nome: "Cuidados e Apoio",
      descricao:
        "Ajuda com idosos, crianas, pessoas com deficincia, apoio emocional ou psicolgico, reforo escolar e acompanhamento para consultas.",
      icone: "hand-heart",
    },
    {
      id: "servicos_e_trabalho",
      nome: "Servios e Trabalho",
      descricao:
        "Reparos domsticos, mudanas, limpezas, trocas de servios, bicos ou doaes de ferramentas para gerao de renda.",
      icone: "tools",
    },
    {
      id: "animais",
      nome: "Ajuda com Animais",
      descricao:
        "Doaes de rao, cuidados temporrios, adoo ou relatos de animais perdidos/encontrados.",
      icone: "car",
    },
    {
      id: "seguranca_alertas",
      nome: "Segurana e Comunicados",
      descricao:
        "Alertas sobre desaparecimentos, riscos locais ou avisos de segurana comunitria.",
      icone: "book-open",
    },
    {
      id: "transporte_solidario",
      nome: "Transporte Solidrio",
      descricao:
        "Caronas, entregas de compras, ajuda com transporte para consultas ou compromissos.",
      icone: "car",
    },
    {
      id: "outros",
      nome: "Outros",
      descricao: "Pedidos ou ofertas que no se encaixam nas categorias acima.",
      icone: "more-horizontal",
    },
  ];

  const getCategoryLabel = (category) => {
    switch (category) {
      case "animais":
        return "Animais";
      case "necessidades_basicas":
        return "Necessidades Bsicas";
      case "transporte_solidario":
        return "Transporte Solidrio";
      case "seguranca_alertas":
        return "Segurana e Alertas";
      case "outros":
        return "Outros";
      case "cuidados_e_apoio":
        return "Cuidados e Apoio";
      case "servicos_e_trabalho":
        return "Servios e Trabalho";
      default:
        return category;
    }
  };

  return (
    <InfoContext.Provider value={{ info, categorias, getCategoryLabel }}>
      {children}
    </InfoContext.Provider>
  );
};

export const useInfo = () => useContext(InfoContext);
